.App {
  text-align: center;

}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: flex;
  align-content: center;
  max-width: 100%;
  height: auto;
}

.App-header > img {
  background-color: #282c34;
  display: flex;
  align-content: center;
  max-width:100%;
  height: auto;
}

.App-link {
  color: #61dafb;
}
